            <!--</div>-->
            <!--</div>-->
            <div class="row position-relative">
                <div class="col-12 col-md-6 cadreImage" :style="{ backgroundImage: 'url(' + require('../assets/images/works/votreplusbeaumarche/votre-plus-beau-marche.jpg') + ')'}"></div>
                <div class="col-12 col-md-6 cadreTexte d-flex align-items-center justify-content-center">
                    <div>
                        <h2>Votre Plus Beau March</h2>
                        <p>Site de vote TF1 pour le plus march de France</p>
                        <button class="button-bk" v-on:click="lien('VotrePlusBeauMarche',$event)">En voir plus</button>
                    </div>
                </div>
            </div>

            <div class="row position-relative">
                <div class="col-12 col-md-6 cadreImage" :style="{ backgroundImage: 'url(' + require('../assets/images/works/carredamour/carredamour.jpg') + ')'}"></div>
                <div class="col-12 col-md-6 cadreTexte d-flex align-items-center justify-content-center">
