                        <button class="button-bk" v-on:click="lien('CarreDAmour',$event)">En voir plus</button>
                    </div>
                </div>
            </div>

            <div class="row position-relative">
                <div class="col-12 col-md-6 cadreImage" :style="{ backgroundImage: 'url(' + require('../assets/images/works/delgaudiogourmet/delgaudiogourmet.jpg') + ')'}"></div>
                <div class="col-12 col-md-6 cadreTexte d-flex align-items-center justify-content-center">
                    <div>
                        <h2>Del Gaudio Gourmet</h2>
                        <p>Site vitrine</p>
                        <button class="button-bk" v-on:click="lien('DelGaudioGourmet',$event)">En voir plus</button>
                    </div>
                </div>
            </div>

            <div class="row position-relative">
                <div class="col-12 col-md-6 cadreImage" :style="{ backgroundImage: 'url(' + require('../assets/images/works/emling/emling.jpg') + ')'}"></div>
                <div class="col-12 col-md-6 cadreTexte d-flex align-items-center justify-content-center">
