            <div class="col-12 cadre0"></div>
        </div>
    </div>
</template>

<script>
    import gsap from 'gsap';
    gsap.registerPlugin(scrollTo);
    import scrollTo from 'gsap/ScrollToPlugin';


    export default {
        name: 'works',
        data() {
            return {
            }
        },
        methods: {
            lien: function (direction, event) {
                let largeur = '50rem';
                if (window.innerWidth > 768) {
                    largeur = '30rem';
                }

                // event.path is undefined running in Firefox
                let path = event.composedPath();

//                768
                gsap.to('.cadre0', {duration:0, height: '100vh'});
                gsap.to(window, {duration: 1, scrollTo: path[2]});
//
//                path[2] >>>> .cadreTexte
                gsap.to(path[2], {duration: 1, height:'40rem', marginTop:'30rem', marginBottom:'20vh'});
//
//                path[3].children[0] >>>> .cadreImage
                gsap.to(path[3].children[0], {duration: 1, width:'100%', maxWidth:'100%', flex:'0 0 100%', height:'40rem', marginTop:largeur, marginLeft:'0', onComplete: this.redirection, onCompleteParams: [direction]});
            },

            redirection: function (direction) {
                this.$router.push({ name: direction });
            },
        }
    }
</script>

<style scoped lang="scss">
    #works{
        background-color: white;
        color: #131314;

        // texte
        .cadreTexte {
            padding-top: 2.5rem;
            padding-bottom: 5rem;
        }

        // image
        .cadreImage{
            position: relative;
            height: 20rem;
            background-size: cover;
            background-position: center;
            z-index: 1;
        }

        @include media-breakpoint-up(md) {
            .row {
                &:nth-child(2n+1) {
                    .cadreTexte{
                        margin-left: 50%;
                    }
                }

                &:nth-child(2n+2) {
                    .cadreImage{
                        margin-left: 50%;
                    }
                }
            }

            .cadreTexte {
                height: 50rem;
                max-height: 800px;
                padding: 0;
            }
            .cadreImage{
                position: absolute;
                height: 50rem;
            }
        }
    }
</style>
