                <div class="col-12 col-lg-8 h-100 menuList">
                    <div class="h-100 p-5 p-lg-0 d-flex flex-column flex-lg-row align-items-center justify-content-around justify-content-lg-end">
                        <router-link class="ms-4" :style="colorStyle" to="/portfolio">PORTFOLIO</router-link>
                        <router-link class="ms-4" :style="colorStyle" to="/quisuisje">QUI SUIS-JE ?</router-link>
                        <a class="ms-4" :style="colorStyle" :href='this.$router.history.base+"/media/cv.pdf"' target="_blank">CV</a>
                        <router-link class="ms-4" :style="colorStyle" to="/contact">CONTACTEZ-MOI</router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Header',
        data() {
            return {
                menuopen: false,
                textColor: "white",
                upscroll: false,
                lastScrollTop: 0
            }
        },
        mounted: function () {
            if(this.$route.meta.theme) this.colorChange();
            window.addEventListener("scroll", this.scroll, false);

            console.log(this.$router.history.base);
        },
        methods: {
            scroll: function () {
                let st = window.pageYOffset || document.documentElement.scrollTop;

                if(st > 0 && st < this.lastScrollTop) {
                    // upscroll code
                    this.upscroll=true;
                } else {
                    // downscroll code
                    this.upscroll=false;
                }
                this.lastScrollTop = st;
            },
            colorChange: function () {
                this.textColor = this.$route.meta.theme;
            },
            mouseMenu: function(){
                this.menuopen = !this.menuopen;
            },
        },
        watch:{
            $route (){
                this.menuopen=false;
                this.colorChange();
            }
        },
        computed: {
            colorStyle() {
                return {
                    '--text-color': this.textColor,
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    #header{
        position: absolute;
        padding: 1.5rem 0;
        top:0;
        width: 100%;
        height: auto;
        left: 0;
        z-index: 9;

        transition: all 0.8s;


        a {
            color: var(--text-color);
            svg.md {
                fill:var(--text-color);
                height: 1rem;
            }

            &.router-link-exact-active.router-link-active:not(.logo) {
                border-bottom: 1px solid #f72359;
            }
        }

        &.upscroll{
            position: fixed;
            background-color: #131314;

            a {
                color: white;

                svg.md .a{
                    fill: white;
                }
            }

            .plate {
                .line {
                    stroke: white;
                }
            }
        }
    }
    .menuList{
        display: none;
        max-height: 400px;
    }
    @include media-breakpoint-up(lg){
        .menuList{
            display: block;
        }
    }

    /*buger menu*/
    .liste{
        svg {
            position: absolute;
            height: 100px;
            width: 100px;
            right: 0;
        }
    }
    .plate {
        position: absolute;
        right: 0.4rem;
        cursor: pointer;
        transform: scale(0.53) translateY(-30px);

        .burger {
            filter: url(#gooeyness);
        }
        .x {
            transform: scale(0);
            transition: transform 400ms;

            .line {
                stroke-width: 5.5px;
            }
        }
        .line {
            fill: none;
            stroke: var(--text-color);
            stroke-width: 5px;
            stroke-linecap: round;
            stroke-linejoin: round;
            transform-origin: 50%;
            transition: stroke-dasharray 500ms 200ms, stroke-dashoffset 500ms 200ms, transform 500ms 200ms;
        }
        .line1 {
            stroke-dasharray: 21 185.62753295898438;
            transition-delay: 0;
        }

        .line2 {
            stroke-dasharray: 21 178.6514129638672;
            transition-delay: 30ms;
        }
        .line3 {
            stroke-dasharray: 21 197.92425537109375;
            transition-delay: 60ms;
        }
        .line4 {
            stroke-dasharray: 21 190.6597137451172;
            transition-delay: 90ms;
        }
        .line5 {
            stroke-dasharray: 21 208.52874755859375;
            transition-delay: 120ms;
        }
        .line6 {
            stroke-dasharray: 21 186.59703063964844;
            transition-delay: 150ms;
        }
    }

    // MENU OPEN
    #header.menuopen{
        position: fixed;
        height: 100%;
        background-color: #131314;

        transition: all 0s;

        a {
            color: white;
        }

        .menuList{
            display: block;
        }

        .plate {
            .line {
                stroke: white;
                transition: stroke-dasharray 500ms, stroke-dashoffset 500ms, transform 500ms;
            }
            .x {
                transform: scale(1);
                transition: transform 400ms 250ms;
            }

            .line1 {
                stroke-dasharray: 5 185.62753295898438;
                stroke-dashoffset: -141px;
            }
            .line2 {
                stroke-dasharray: 5 178.6514129638672;
                stroke-dashoffset: -137px;
            }
            .line3 {
                stroke-dasharray: 5 197.92425537109375;
                stroke-dashoffset: -176px;
            }
            .line4 {
                stroke-dasharray: 5 190.6597137451172;
                stroke-dashoffset: -159px;
            }
            .line5 {
                stroke-dasharray: 5 208.52874755859375;
                stroke-dashoffset: -139px;
            }
            .line6 {
                stroke-dasharray: 5 186.59703063964844;
                stroke-dashoffset: -176px;
            }
        }

        @include media-breakpoint-up(lg){
            position: absolute;
            height: auto;
            background-color: transparent;

            transition: all 1s;

            &.upscroll{
                position: fixed;
                background-color: #131314;

                .plate {
                    .line {
                        stroke: white;
                    }
                }
            }
        }
    }
</style>
