<script>
    import {TweenLite, Back} from 'gsap';

    export default {
        name: 'Welcome',
        components: {
        },
        data() {
            return {

            }
        },
        methods: {
            mouseOver: function(e){
                e.target.style.setProperty('--x', e.offsetX - e.target.offsetWidth/2 + "px");
                e.target.style.setProperty('--y', e.offsetY + e.target.offsetHeight+5 + "px");
            },
            mouseScroll: function () {
                TweenLite.to(window,1,{scrollTo:{y:"#skills"}, ease: Back.easeOut.config(1.7)});
            }
        }
    }
</script>

<style lang="scss">
    @keyframes bounce {
        0%, 20%, 50%, 80%, 100% {
            transform: translateY(0);
        }
        40% {
            transform: translateY(-10px);
        }
        60% {
            transform: translateY(-2px);
        }
    }

    .barScroll{
        position: sticky;
        background-color: white;
        height: 25px;
        bottom: 0;
        z-index: 10;

        cursor: pointer;

        .arrow {
            div{
                position: absolute;
                width: 14px;
                left: 50%;
                top: -8px;
                transform: translateX(-50%);

                svg {
                    position: absolute;
                }
            }

            div:first-child svg{
                animation: bounce 2s infinite 50ms;
            }

            div:last-child svg{
                animation: bounce 2s infinite;
            }
        }

        .barBump{
            position: absolute;

            width: 100%;
            height: 25px;

            top:2px; // dcalage pour le filtre
            filter:url('#goo');
            background-color: white;

            div{
                position: absolute;
                width: 200px;
                left: 50%;
                transform: translateX(-50%);

                svg {
                    position: absolute;
                    fill:white;
                    top:-30px;
                }
            }

        }
    }

    #welcome {
        /*background: linear-gradient(135deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);*/
        background: linear-gradient(135deg, #262626, #2e2c25, #2d3032, black);
        background-size: 400% 400%;
        animation: gradient 15s ease infinite;

        @keyframes gradient {
            0% {
                background-position: 0% 50%;
            }
            50% {
                background-position: 100% 50%;
            }
            100% {
                background-position: 0% 50%;
            }
        }

        overflow: hidden;

        position: relative;
        width: 100%;
        height: 100vh;

        z-index: 1;

        h1{
            font-weight: 900;
        }

        .md{
            fill: white;
            height: 10rem;
        }

        .container{
            position: relative;
            z-index: 10;
        }
    }
</style>
