import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    meta: { theme: 'white' },
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
    {
        path: '/portfolio',
        name: 'Portfolio',
        meta: { theme: 'black' },
        component: () => import('../views/Portfolio.vue'),
        props: (route) => ({ query: route.query.player })
    },
    {
        path: '/quisuisje',
        name: 'QuiSuisJe',
        meta: { theme: 'black' },
        component: () => import('../views/QuiSuisJe.vue'),
        props: (route) => ({ query: route.query.player })
    },
    {
        path: '/votreplusbeaumarche',
        name: 'VotrePlusBeauMarche',
        meta: { theme: 'black' },
        component: () => import('../views/VotrePlusBeauMarche.vue'),
        props: (route) => ({ query: route.query.player })
    },
    {
        path: '/carredamour',
        name: 'CarreDAmour',
        meta: { theme: 'black' },
        component: () => import('../views/CarreDAmour.vue'),
        props: (route) => ({ query: route.query.player })
    },
    {
        path: '/delgaudiogourmet',
        name: 'DelGaudioGourmet',
        meta: { theme: 'black' },
        component: () => import('../views/DelGaudioGourmet.vue'),
        props: (route) => ({ query: route.query.player })
    },
    {
        path: '/emling',
        name: 'Emling',
        meta: { theme: 'black' },
        component: () => import('../views/Emling.vue'),
        props: (route) => ({ query: route.query.player })
    },
    {
        path: '/familydrugstore',
        name: 'FamilyDrugstore',
        meta: { theme: 'black' },
        component: () => import('../views/FamilyDrugstore.vue'),
        props: (route) => ({ query: route.query.player })
    },
    {
        path: '/logotype',
        name: 'Logotype',
        meta: { theme: 'black' },
        component: () => import('../views/Logotype.vue'),
        props: (route) => ({ query: route.query.player })
    },
    {
        path: '/contact',
        name: 'contact',
        meta: { theme: 'white' },
        component: () => import('../views/Contact.vue'),
        props: (route) => ({ query: route.query.player })
    },
    {
        path: '/404',
        name: '404',
        meta: { theme: 'white' },
        component: () => import('../views/404.vue'),
        props: (route) => ({ query: route.query.player })
    },
    { path: '*', redirect: '/404' }
];

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(){
      return { y: 0 }
  }
});

export default router
