<template>
    <div id="skills" class="text-dark position-relative">
        <div class="backgroundGradient">
            <section>
                <div class="color"></div>
                <div class="color"></div>
                <div class="color"></div>
            </section>
        </div>
        <main class="container position-relative">
            <div class="row justify-content-center align-items-center">
                <div class="col-12 col-lg-8">
                    <h3 class="mb-5">MES COMPTENCES</h3>
                    <div>
                        <h2 class="texth2">{{ texteTitre }}</h2>
                        <span class="textspan">{{ texteSkills }}</span>
                    </div>
                </div>

                <div class="col-12 col-lg-4 mt-5 mt-lg-0 text-center">
                    <div class="container">
                        <div class="row justify-content-center align-items-center cv">
                            <div class="col-9">
                                <h4>MON CV</h4>
                                <div class="mt-4">Besoin d'enregistrer ou d'imprimer mon CV ?</div>
                                <a class="button-bk mt-4 mb-4" :href='this.$router.history.base+"/media/cv.pdf"' target="_blank">Tlcharger mon CV</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                loopSkills: 0,
                texteTitre: 'CRATION DE SITE WEB',
                texteSkills: 'HTML5 / CSS3 / JavaScript / GSAP / Vue.js / PHP / Bootstrap / WordPress / Prestashop',
                creativesInfos: [
                    {
                        titre: "CRATION DE SITE WEB",
                        skills: "HTML5 / CSS3 / JavaScript / GSAP  / Vue.js / PHP / Bootstrap / WordPress / Prestashop"
                    },{
                        titre: "WEBDESIGN",
                        skills: "Identit visuelle / Charte graphique / UX Design / UI Design"
                    },{
                        titre: "LOGO & IDENTIT VISUELLE",
                        skills: "Adobe Photoshop / Adobe Illustrator / Adobe Indesign"
                    },{
                        titre: "NEWSLETTER & RSEAUX SOCIAUX",
                        skills: "Intgration / Sendiblue, Mailjet, Mailchimp / Stories / Bannires Display / Snackable contents"
                    },{
                        titre: "ANIMATION & BANNIRES HTML5",
                        skills: "GSAP  / Adobe After Effects / Adobe Animate"
                    },{
                        titre: "GRAPHISME & ILLUSTRATION",
                        skills: "Adobe Photoshop / Adobe Illustrator"
                    },
                ]
            }
        },
        methods: {
            callFunction() {
                let v = this;
                setTimeout(function () {
                    v.loopSkills++;
                    if(v.loopSkills > v.creativesInfos.length-1) v.loopSkills=0;

                    v.texteTitre=v.creativesInfos[v.loopSkills].titre;
                    v.texteSkills=v.creativesInfos[v.loopSkills].skills;
                    v.callFunction();
                }, 3000);
            },
        },
        mounted() {
            this.callFunction()
        },
        destroyed() {
            clearTimeout(this.timeout);
        },
    }
</script>

<style lang="scss" scoped>
    .backgroundGradient{
        position: absolute;
        width: 100%;
        height: 100%;
        overflow: hidden;

        section{
            display: flex;
            justify-content: center;
            align-items: center;
            min-height: 100%;
            background: linear-gradient(to bottom, #f1f4f9,#dff1ff);
        }

        section .color {
            position: absolute;
            filter: blur(150px);
        }

        section .color:nth-child(1){
            top: -250px;
            width: 100%;
            height: 100%;
            background: #ff359b;

            animation: slidein1 3s infinite alternate;
        }

        @keyframes slidein1 {
            to {
                transform:rotate(10deg);
            }
        }

        section .color:nth-child(2){
            bottom: -150px;
            left: 100px;
            width: 90%;
            height: 90%;
            background: #fffd87;
        }

        section .color:nth-child(3){
            bottom: 50px;
            right: 100px;
            width: 30%;
            height: 30%;
            background: #00d2ff;

            animation: slidein3 6s infinite alternate;
        }

        @keyframes slidein3 {
            40% {
                bottom: 150px;
                right: -150px;
                transform:rotate(30deg);
            }
            70% {
                bottom: 50px;
                right: -150px;
                transform:rotate(45deg);
            }
            100% {
                bottom: 50px;
                right: 100px;
                transform:rotate(60deg);
            }
        }
    }

    main.container {
        a{
            color: black;

            &:hover{
                color: white;
            }
        }

        padding-top: 10rem;
        padding-bottom: 10rem;

        h4 {
            font-weight: 900;
        }

        h3 {
            font-weight: 900;
            font-size: 3em;
        }

        h2 {
            font-weight: 100;
        }

        .cv {
            min-height: 300px;
            @include media-breakpoint-up(lg) {
                min-height: 400px;
            }
            background: rgba(255,255,255,0.1);
            border-radius: 2rem;

            box-shadow: 0 25px 45px rgba(0,0,0,0.1);
            border: 1px solid rgba(255,255,255,0.5);
            border-right: 1px solid rgba(255,255,255,0.2);
            border-bottom: 1px solid rgba(255,255,255,0.2);
        }
    }

</style>
